.features-group {
  padding: 64px 0;
  background-color: white; }
  @media (max-width: 1200px) {
    .features-group {
      padding: 32px 0; } }

.footer {
  background-color: #262626; }
  .footer__group {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media (max-width: 520px) {
      .footer__group {
        flex-direction: column; } }
  .footer__text {
    color: #595959;
    font-size: 14px; }
    @media (max-width: 520px) {
      .footer__text {
        text-align: center; } }
  .footer__link {
    text-decoration: none;
    border-bottom: 1px solid #333333;
    color: #595959; }

.header {
  height: 64px;
  background: #febb34; }
  @media (max-width: 520px) {
    .header {
      height: 48px; } }
  .header__text {
    color: black;
    font-family: "Open Sans", Helvetica, sans-serif;
    font-size: 28px;
    font-weight: 600;
    line-height: 64px; }
    @media (max-width: 520px) {
      .header__text {
        font-size: 24px;
        line-height: 48px; } }

.link-button {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 8px 32px;
  text-decoration: none;
  background-color: #333333;
  border-radius: 8px;
  min-width: 125px;
  box-sizing: border-box; }
  .link-button__text {
    flex: 0 0 auto;
    font-family: "Open Sans", Helvetica, sans-serif;
    font-weight: 700;
    color: #f7f7f7;
    text-align: center; }
  .link-button__text + .link-button__image {
    margin-left: 16px; }
  .link-button__image {
    flex: 0 0 auto;
    height: 30px; }

.terminal {
  padding: 32px 0; }
  .terminal__window {
    max-width: 768px;
    margin: 0 auto;
    border-radius: 8px;
    padding: 24px;
    background-color: #262626;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3); }
    @media (max-width: 960px) {
      .terminal__window {
        max-width: 400px; } }
  .terminal__line {
    color: white;
    font-family: "Roboto Mono", monospace;
    line-height: 1.5em;
    margin-bottom: 0; }
    @media (max-width: 960px) {
      .terminal__line {
        white-space: pre-wrap;
        word-break: break-word; } }
    @media (max-width: 520px) {
      .terminal__line {
        font-size: 14px; } }
  .terminal__line--response {
    color: #999999; }
  .terminal__controls-image {
    height: 16px;
    margin-bottom: 8px; }
    @media (max-width: 350px) {
      .terminal__controls-image {
        height: 12px; } }

.quick-start {
  padding: 32px 0;
  display: flex;
  align-items: center; }
  .quick-start__command {
    flex: 0 0 auto;
    font-family: "Roboto Mono", monospace;
    background-color: #ededed;
    line-height: 40px;
    padding: 0 18px;
    border-radius: 4px; }
    @media (max-width: 768px) {
      .quick-start__command {
        font-size: 18px; } }
    @media (max-width: 520px) {
