  .quick-start__link {
    flex: 0 0 auto;
    height: 40px; }
    @media (max-width: 520px) {
      .quick-start__link {
        height: 32px; } }
    .quick-start__link + .quick-start__link {
      margin-left: 16px; }
      @media (max-width: 520px) {
        .quick-start__link + .quick-start__link {
          margin-left: 8px; } }
      @media (max-width: 350px) {
        .quick-start__link + .quick-start__link {
          margin-left: 4px; } }
  .quick-start__image {
    max-height: 40px; }
    @media (max-width: 520px) {
      .quick-start__image {
        height: 32px; } }
  .quick-start__command + .quick-start__link {
    margin-left: 16px; }
    @media (max-width: 520px) {
      .quick-start__command + .quick-start__link {
        margin-left: 8px; } }
    @media (max-width: 350px) {
      .quick-start__command + .quick-start__link {
        margin-left: 4px; } }

.title {
